/** @jsx jsx */
import React from "react"
import { jsx, useColorMode } from "theme-ui"
import theme from "gatsby-plugin-theme-ui"

export default function ColorModeSwitcher(props) {
  const [colorMode, setColorMode] = useColorMode()
  const currentColorMode = colorMode
  const remainingColorModes = Object.keys(theme.colors.modes)
  const allColorModes = ["default", ...remainingColorModes]
  const currentColorModeIndex = allColorModes.indexOf(currentColorMode)
  const nextColorModeIndex = (currentColorModeIndex + 1) % allColorModes.length
  const nextColorMode = allColorModes[nextColorModeIndex]

  return (
    <>
      <button
        className="btn btn-primary"
        onClick={e => {
          setColorMode(nextColorMode)
        }}
      >
        {props.text}
      </button>
    </>
  )
}
